body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', sans-serif;
  background: linear-gradient(to right, #314755, #26a0da);
}

 @font-face {
    font-family: 'Pixel Technology';
    font-style: normal;
    font-weight: normal;
    src: local('Pixel Technology'), url(/robofriends/static/media/pixtech.42e767fe.woff) format('woff');
    }

    h1 {
        font-family: 'Pixel Technology';
        font-weight: 200;
        background-image: linear-gradient(to right, #000000, #434343);
        -webkit-background-clip: text;
                background-clip: text;
        color: transparent;
    }
